@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

body {
  font-family: "Roboto", sans-serif;
  background-image: url("/src/assets/background-image-copy.png");
  background-size: cover;
  height: 100vh;
}

.title {
  text-align: center;
  color: #186cd4;
  margin-bottom: 30px;
  text-shadow: 3px 1px 2px #ffffff;
}

#form {
  width: 350px;
  background-color: #000;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 50px;
  border-radius: 10px;
  border: 3px solid #ffffff;
}
#form .input-group {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
#form .input-group label {
  color: #ffffff;
  margin-top: 10px;
}
#form .input-group > input {
  width: 100%;
  padding: 10px 20px;
  outline: none;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
}
#form .input-group .error {
  margin-top: 10px;
  color: rgb(233, 20, 20);
}
#form .input-group.success input {
  outline: 3px solid #5eeb2f;
}
#form .input-group.error input {
  outline: 3px solid rgb(233, 20, 20);
}

button {
  width: 100%;
  margin-top: 20px;
  padding: 10px;
  background-color: rgb(213, 150, 33);
  border: none;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid #ffffff;
}

button:hover {
  background-color: rgb(51, 44, 159);
  color: #ffffff;
  border: 1px solid #ffffff;
  transition: 0.2s ease-out;
}